import { ReactElement } from 'react';

import { WithAria } from '../../../lib/index.js';
import { EntityCardShimmer } from '../EntityCardShimmer/EntityCardShimmer.js';

interface Props extends WithAria {
    itemClassName: string;
    count?: number;
    round?: boolean;
    centered?: boolean;
}

export const GridShimmer = ({
    'aria-label': ariaLabel,
    itemClassName,
    round,
    centered,
    count = 10
}: Props): ReactElement[] => {
    const items = Array.from(Array(count).keys());

    return items.map((index: number) => (
        <EntityCardShimmer
            key={index}
            className={itemClassName}
            round={round}
            centered={centered}
            aria-label={ariaLabel}
        />
    ));
};
