import { clsx } from 'clsx';
import { ReactElement } from 'react';

import { Image } from '@yandex-music-int/sol/components/Image';
import { type TabProps as BaseTabProps, Tab as BaseTab } from '@yandex-music-int/sol/components/Tabs';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '../../lib/index.js';

import styles from './Tab.module.scss';

interface Props extends WithClassName, BaseTabProps {
    value: number;
    title?: string;
    subtitle?: string;
    covers?: string[];
    withCovers?: boolean;
}

export const Tab = ({ className, value, title, subtitle, covers, withCovers, ...props }: Props): ReactElement => {
    return (
        <BaseTab
            value={value}
            className={clsx(
                styles.root,
                {
                    [styles.tab_size_s]: !subtitle && covers?.length,
                    [styles.tab_size_m]: !subtitle && !covers?.length,
                    [styles.tab_size_l]: subtitle && covers?.length === 1,
                    [styles.tab_withCovers]: withCovers
                },
                className
            )}
            {...props}
        >
            {Boolean(covers?.length) && (
                <span className={styles.covers}>
                    {covers?.map((uri: string) => (
                        <Image className={styles.image} key={uri} src={uri} fit="cover" size={50} withAvatarReplace />
                    ))}
                </span>
            )}
            <span className={styles.description}>
                <Caption className={styles.title} variant="div" type="controls" size="m" weight="bold" lineClamp={1}>
                    {title}
                </Caption>
                {Boolean(subtitle) && (
                    <Caption
                        className={styles.subtitle}
                        variant="div"
                        type="controls"
                        size="s"
                        weight="medium"
                        lineClamp={1}
                    >
                        {subtitle}
                    </Caption>
                )}
            </span>
        </BaseTab>
    );
};
