import { clsx } from 'clsx';
import { forwardRef, ReactElement, Ref } from 'react';

import { TabProps, Tabs, TabsState } from '@yandex-music-int/sol/components/Tabs';

import { WithAria, WithClassName } from '../../lib/index.js';
import { TabShimmer, TabShimmerWithCovers } from '../Shimmer/TabShimmer/TabShimmer.js';

import styles from './TabCarousel.module.scss';

interface Props extends TabsState, WithClassName, WithAria {
    shimmer?: ReactElement;
    isLoading?: boolean;
    children?: Array<ReactElement<TabProps>>;
    forwardRef?: Ref<HTMLDivElement>;
}

const TabCarouselComponent = ({
    value,
    shimmer,
    children,
    isLoading,
    onTabChange,
    className,
    forwardRef: ref,
    ...props
}: Props): ReactElement => {
    if (isLoading && shimmer) {
        return shimmer;
    }

    return (
        <Tabs className={clsx(styles.root, className)} value={value} onTabChange={onTabChange} ref={ref} {...props}>
            {children}
        </Tabs>
    );
};

interface ShimmerProps extends WithClassName {}

export const TabCarouselShimmer = ({ className }: ShimmerProps): ReactElement => {
    return (
        <div className={clsx(styles.root, className)}>
            {Array.from({ length: 2 }, (value: unknown, index: number) => {
                return <TabShimmer key={index} />;
            })}
        </div>
    );
};

export const TabCarouselShimmerWithCovers = ({ className }: ShimmerProps): ReactElement => {
    return (
        <div className={clsx(styles.root, className)}>
            {Array.from({ length: 2 }, (value: unknown, index: number) => {
                return <TabShimmerWithCovers key={index} />;
            })}
        </div>
    );
};

export const TabCarousel = forwardRef<HTMLDivElement, Props>(
    ({ ...props }: Props, ref: Ref<HTMLDivElement>): ReactElement => (
        <TabCarouselComponent forwardRef={ref} {...props} />
    )
);
