'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactElement, useCallback, useEffect, useState } from 'react';
import { useIntl } from 'react-intl';
import { A11y, Keyboard, Mousewheel } from 'swiper/modules';
import { Swiper, SwiperClass, SwiperSlide, useSwiper } from 'swiper/react';

import { useToggle } from '@yandex-music-int/sol/common/hooks/useToggle.js';

import { SlideModel } from '@/entities/slides/index.js';

import { LayoutName, useStore } from '@/shared/lib/index.js';

import { SliderControls } from '../Controls/SliderControls.js';
import { Slide } from '../Slide/Slide.js';

import styles from './Slider.module.scss';

interface Props {
    items: SlideModel[];
}

const SLIDE_SPACE_BETWEEN = 16;
const MOUSEWHEEL_THRESHOALD_TIME = 500;

export const Slider = observer(({ items }: Props): ReactElement => {
    const { settings, slides } = useStore();
    const { formatMessage } = useIntl();
    const { state: sliderVisibility, toggleTrue: setTrueSliderVisibility } = useToggle(false);
    const swiper = useSwiper();

    const [isFirstSlide, setIsFirstSlide] = useState(true);
    const [isLastSlide, setIsLastSlide] = useState(false);

    useEffect(() => {
        setTrueSliderVisibility();
        return () => {
            swiper?.slideTo(0);
            slides.setActiveSlide(0);
        };
    }, [setTrueSliderVisibility, swiper, slides]);

    const isMobile = settings.layout === LayoutName.Mobile;

    const indexChangeHandler = useCallback(
        (swiperClass: SwiperClass) => {
            setIsFirstSlide(swiperClass.isBeginning);
            setIsLastSlide(swiperClass.isEnd);
            slides.setActiveSlide(swiperClass.activeIndex);
        },
        [slides]
    );

    return (
        <div
            className={clsx(styles.root, {
                [styles.root_visible]: sliderVisibility
            })}
        >
            <Swiper
                direction="vertical"
                centeredSlides={!isMobile}
                slidesPerView="auto"
                spaceBetween={SLIDE_SPACE_BETWEEN}
                className={styles.container}
                keyboard={{
                    enabled: true
                }}
                modules={[Keyboard, A11y, Mousewheel]}
                onActiveIndexChange={indexChangeHandler}
                a11y={{
                    enabled: true,
                    containerMessage: formatMessage({ id: 'page.results-of-the-year' })
                }}
                mousewheel={{
                    thresholdTime: MOUSEWHEEL_THRESHOALD_TIME
                }}
            >
                {items.map((item: SlideModel) => (
                    <SwiperSlide key={item.id}>
                        <Slide slide={item} />
                    </SwiperSlide>
                ))}
                <SliderControls isFirstSlide={isFirstSlide} isLastSlide={isLastSlide} />
            </Swiper>
        </div>
    );
});
