'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { notFound } from 'next/navigation.js';
import { ReactElement, use, useEffect, useMemo } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Logo } from '@yandex-music-int/sol/components/Logo';
import { LogoNew } from '@yandex-music-int/sol/components/LogoNew';
import { SROnly } from '@yandex-music-int/sol/components/SROnly';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';
import { AudioPlayerStatus } from '@yandex-music-int/sonata';

import { Slider } from '@/widgets/slider/index.js';

import { useHistoryBackOrForward } from '@/features/navigation/index.js';

import { SlideModel, SlidesConsumer } from '@/entities/slides/index.js';

import { LoadingState, useSonata, useStore, Experiments } from '@/shared/lib/index.js';
import { Link } from '@/shared/ui/index.js';

import styles from './Slides.module.scss';

interface Props {
    slidesConsumer: SlidesConsumer;
    artistId?: string;
}

function isEmptySlides(state: LoadingState, items: SlideModel[]): boolean {
    return (state === LoadingState.RESOLVE || state === LoadingState.REJECT) && items.length === 0;
}

export const SlidesPage = observer(({ slidesConsumer, artistId }: Props): ReactElement => {
    const { formatMessage } = useIntl();
    const { slides, experiments, sonataState, settings } = useStore();
    const sonata = useSonata();

    const safePlatformBackHref = useMemo(() => {
        if (process.env.BUILD_TYPE === 'landing') {
            return `https://music.yandex.${settings.clientTld}`;
        }
        return '/';
    }, [settings.clientTld]);

    const { moveBack: handleBackClick } = useHistoryBackOrForward(safePlatformBackHref);

    switch (slidesConsumer) {
        case SlidesConsumer.USER: {
            if (slides.userSlidesLoadingState === LoadingState.IDLE) {
                use(slides.getUserSlides());
            }
            break;
        }
        case SlidesConsumer.ARTIST: {
            if (artistId && slides.artistSlidesLoadingState === LoadingState.IDLE) {
                use(
                    slides.getArtistSlides({
                        artistId
                    })
                );
            }
            break;
        }
    }

    useEffect(() => {
        if (sonataState.status === AudioPlayerStatus.PLAYING) {
            sonata?.togglePause();
        }
    }, [sonata, sonataState.status]);

    useEffect(() => {
        return () => {
            if (
                slides.userSlidesLoadingState === LoadingState.REJECT ||
                slides.userSlidesLoadingState === LoadingState.PENDING
            ) {
                slides.userReset();
            }
            slides.artistReset();
        };
    }, [slides]);

    const items = useMemo(() => {
        switch (slidesConsumer) {
            case SlidesConsumer.USER:
                return slides.userItems;
            case SlidesConsumer.ARTIST:
                return slides.artistItems;
        }
    }, [slides.artistItems, slides.userItems, slidesConsumer]);

    const logo = useMemo(() => {
        if (experiments.checkExperiment(Experiments.webLogo2023, 'on2023')) {
            return <LogoNew alignIcon="center" lang="ru" />;
        }
        return <Logo tenant="yandex" variant="ru" />;
    }, [experiments]);

    useEffect(() => {
        if (experiments.checkExperiment('WebRewind2023', 'on')) {
            switch (slidesConsumer) {
                case SlidesConsumer.USER: {
                    if (isEmptySlides(slides.userSlidesLoadingState, items)) {
                        notFound();
                    }
                    break;
                }
                case SlidesConsumer.ARTIST: {
                    if (isEmptySlides(slides.artistSlidesLoadingState, items)) {
                        notFound();
                    }
                    break;
                }
            }
        } else {
            notFound();
        }
    }, [
        experiments,
        items,
        items.length,
        slides.artistSlidesLoadingState,
        slides.userSlidesLoadingState,
        slidesConsumer
    ]);

    return (
        <div className={styles.root}>
            <SROnly>
                <Heading variant="h1">
                    <FormattedMessage id="page.results-of-the-year" />
                </Heading>
            </SROnly>
            <header className={styles.header}>
                <Link
                    className={clsx(styles.logoLink, {
                        [styles.logoLink_isOld]: !experiments.checkExperiment(Experiments.webLogo2023, 'on2023')
                    })}
                    href={safePlatformBackHref}
                    aria-label={formatMessage({ id: 'navigation.logo' })}
                >
                    {logo}
                </Link>

                <div className={styles.slot} />

                {items?.length > 0 && (
                    <div className={styles.slidesResult}>
                        <Caption variant="span" className={styles.activeItem} type="text" size="l" weight="medium">
                            {Number(slides.activeSlide) + 1}
                        </Caption>
                        <Caption variant="span" type="text" size="l" weight="medium">
                            /
                        </Caption>
                        <Caption variant="span" type="text" size="l" weight="medium">
                            {items?.length}
                        </Caption>
                    </div>
                )}
                <Button
                    aria-label={formatMessage({ id: 'navigation.go-back' })}
                    radius="round"
                    size="xs"
                    icon={<Icon size="xs" variant="close" key="handleBackIcon" />}
                    onClick={handleBackClick}
                    className={clsx(styles.backButton, styles.desktopBackButton)}
                    role="link"
                />
                <Button
                    variant="text"
                    aria-label={formatMessage({ id: 'navigation.go-back' })}
                    radius="round"
                    size="xs"
                    icon={<Icon size="xs" variant="arrowLeft" key="handleBackIcon" />}
                    onClick={handleBackClick}
                    className={clsx(styles.backButton, styles.mobileBackButton)}
                    role="link"
                />
            </header>
            <Slider items={items} />
        </div>
    );
});
