'use client';

import { clsx } from 'clsx';
import { ReactElement } from 'react';
import { useSwiperSlide } from 'swiper/react';

import { Paper } from '@yandex-music-int/sol/components/Paper';

import { SlideCard, SlideModel } from '@/entities/slides/index.js';

import styles from './Slide.module.scss';

interface Props {
    slide: SlideModel;
}

export const Slide = ({ slide }: Props): ReactElement => {
    const { isActive } = useSwiperSlide();

    return (
        <Paper className={clsx(styles.root, { [styles.root_isActive]: isActive })} radius="l" tabIndex={0}>
            <SlideCard slide={slide} isActive={isActive} />
        </Paper>
    );
};
