'use client';

import { clsx } from 'clsx';
import { ReactElement, useCallback } from 'react';
import { FormattedMessage } from 'react-intl';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Heading, Caption } from '@yandex-music-int/sol/components/Typography';

import { NavigationControls } from '@/features/navigation/index.js';

import { useNavigate, useStore, WithClassName } from '@/shared/lib/index.js';

import styles from './NotFound.module.scss';

interface Props extends WithClassName {}

export const NotFound = ({ className }: Props): ReactElement => {
    const { experiments } = useStore();
    const navigate = useNavigate();

    const goToMainPage = useCallback(() => {
        navigate('/');
    }, [navigate]);

    return (
        <div className={clsx(styles.root, className)}>
            <NavigationControls withBackwardFallback="/" className={styles.navigation} withForwardControl={false} />

            <div className={styles.content}>
                {experiments.checkExperiment('webLogo2023', 'on2023') ? (
                    <Icon className={styles.icon} variant="musicLogoNew" size="xxl" />
                ) : (
                    <Icon className={styles.icon} variant="musicLogo" size="xxl" />
                )}

                <Heading className={clsx(styles.title, styles.important)} variant="h3" size="xs">
                    <FormattedMessage id="page-error.page-does-not-exist" />
                </Heading>

                <Caption
                    className={clsx(styles.text, styles.important)}
                    variant="span"
                    type="text"
                    size="l"
                    weight="normal"
                >
                    <FormattedMessage id="page-error.page-does-not-exist-description" />
                </Caption>

                <Button
                    onClick={goToMainPage}
                    className={styles.button}
                    role="link"
                    color="secondary"
                    size="l"
                    radius="xxxl"
                >
                    <Caption type="controls" variant="span" size="m">
                        <FormattedMessage id="navigation.page-main" />
                    </Caption>
                </Button>
            </div>
        </div>
    );
};
