import { CSSProperties, ReactElement, useCallback } from 'react';
import { useIntl } from 'react-intl';
import { useSwiper } from 'swiper/react';

import { Button } from '@yandex-music-int/sol/components/Button';
import { Icon } from '@yandex-music-int/sol/components/Icon';

import styles from './SliderControls.module.scss';

interface Props {
    isFirstSlide: boolean;
    isLastSlide: boolean;
    style?: CSSProperties;
}

export const SliderControls = ({ isFirstSlide, isLastSlide, style }: Props): ReactElement => {
    const swiper = useSwiper();
    const { formatMessage } = useIntl();

    const slidePrev = useCallback(() => {
        swiper.slidePrev();
    }, [swiper]);

    const slideNext = useCallback(() => {
        swiper.slideNext();
    }, [swiper]);

    return (
        <div className={styles.root} style={style}>
            <Button
                variant="outline"
                iconClassName={styles.top}
                size="m"
                radius="round"
                onClick={slidePrev}
                disabled={isFirstSlide}
                icon={<Icon variant="arrowDown" key="prevIcon" />}
                aria-label={formatMessage({ id: 'slider.prev-slide' })}
            />
            <Button
                variant="outline"
                size="m"
                radius="round"
                onClick={slideNext}
                disabled={isLastSlide}
                icon={<Icon variant="arrowDown" key="nextIcon" />}
                aria-label={formatMessage({ id: 'slider.next-slide' })}
            />
        </div>
    );
};
